<template>
  <div class="link-log">
    <!-- 发生时间 -->
    <el-tag type="info" size="mini" style="font-weight: 500;">{{ data.time }}</el-tag>
    <!-- 日志类型 -->
    <template>
      <template v-if="data.errorText">
        <span class="log-type error">错误</span>
        <el-tag type="danger" size="mini">{{ data.errorText }}</el-tag>
      </template>
      <template v-else>
        <span class="log-type">信息</span>
        <el-tag v-if="data.successText" :type="data.successTextType || 'success'">{{
          data.successText
        }}</el-tag>
      </template>
    </template>
    <!-- 事件自定义名称 -->
    <el-tag v-if="eventNameTag(data)" class="event-name-tag" size="mini">{{ eventNameTag(data) }}</el-tag>
    <!-- 展示被操作的对象 -->
    <template v-if="data.target && data.target.elementId">
      {{ data.type === '变量赋值' ? '被赋值对象' : '被操作对象' }}:
      <comp-tag :log="data"></comp-tag>
    </template>
    <!-- 各个变量的值 -->
    <template v-if="data.tagList && data.tagList.length">
      <span v-if="data.type === '变量赋值'">被赋值变量</span>
      <variable-tag v-for="tag in data.tagList" :key="tag.key" :data="tag"></variable-tag>
    </template>
    <!-- 文字消息 -->
    <span v-if="data.message">{{ data.message }}</span>
    <template v-if="data.children && data.children.length">
      <el-button type="default" @click="$emit('visible-change')" size="mini"
        >{{ visible ? '收起' : '展开'
        }}<i
          :class="{
            'el-icon-arrow-right': !visible,
            'el-icon-arrow-down': visible,
          }"
        ></i
      ></el-button>
      <!-- <el-button type="default" @click="$emit('sort-change')" size="mini"
        >{{ desc ? '倒序' : '正序'
        }}<i
          :class="{
            'el-icon-caret-bottom': !desc,
            'el-icon-caret-top': desc,
          }"
        ></i
      ></el-button> -->
    </template>
  </div>
</template>

<script>
import compTag from '../compTag'
import variableTag from '../variableTag'
export default {
  components: {
    compTag,
    variableTag
  },
  props: {
    data: {
      type: Object,
    },
    visible: {
      type: Boolean,
    },
    desc: {
      type: Boolean,
    },
  },
  data() {
    return {}
  },
  created() {},
  mounted() {},
  methods: {
    eventNameTag(data) {
      const { type = '', eventName = '' } = data
      return type + (type && eventName ? '-' : '') + eventName
    }
  },

  computed: {},

  watch: {},
}
</script>
<style lang='less' scoped>
.link-log {
  background-color: #f6f6f6;
  padding: 5px;
  white-space: normal;
  border-left: 1px solid #dfe6ec;
  border-bottom: 1px solid #dfe6ec;
  .log-type {
    font-weight: bold;
    color: gray;
    &.error {
      color: #f56c6c;
    }
  }
}
</style>